import { AxiosRequestConfig } from 'axios';
/**
 * 
 * 
 */

/**
 * @description 
 *
 * @keyWord 
 * @pageNo 
 * @pageSize 
 * @sortDesc 
 * @sortName 
 */
export interface BaseSearchFace {
  keyWord?: string; // 
  pageNo?: number; // 
  pageSize?: number; // 
  sortDesc?: boolean; // 
  sortName?: string; // 
}

/**
 * @description 
 *
 * @pageNo 
 * @pageSize 
 * @seqAsc 
 * @seqColumn 
 * @keyWord 
 */
export class BaseSearchClass {
  keyWord?: string;

  pageNo?: number;

  pageSize?: number;

  seqAsc?: boolean;

  seqColumn?: string;

  //  
  setKeyWord(val: string | undefined) {
    this.keyWord = val;
  }

  constructor(data: BaseSearchFace) {
    // base
    this.pageNo = data.pageNo;
    this.pageSize = data.pageSize;

    // 
    if (data.sortDesc !== undefined && data.sortName) {
      this.seqAsc = data.sortDesc;
      // 
      this.seqColumn = data.sortName;
    }
  }
}

/**
 * @description Antd-Upload-v3 
 *
 * @uid 
 * @name 
 * @status  uploading done error removed
 * @originFIleObj 
 *
 * @remark interfaceantd
 */
export interface Afile {
  uid: string; //  id 
  name: string; // 
  status: 'uploading' | 'done' | 'error' | 'removed'; // uploading done error removed
  response: {}; // '{"status": "success"}', // 
  linkProps: {}; // '{"download": "image"}', //  HTML 
  xhr: XMLHttpRequest; // 'XMLHttpRequest{ ... }', // XMLHttpRequest Header

  // 
  originFileObj: File; // 
}

export interface Bread {
  breadcrumbName: string;
  path: string;
}

/**
 * 
 */
export type ClassKey<T, R = any> = {
  readonly [k in keyof T]: R;
};

/**
 * axios
 */
export type ReqConfig = AxiosRequestConfig & {
  /**
   * useMockmock
   */
  useMock?: boolean;

  /**
   * 
   */
  url: string;

  /**
   * 
   */
  method: string;
};
